.tas-pikto {
  display: block;
  position: relative;
  background: no-repeat top left;
  background-size: cover;
  width: 100%;
  height: 100%;
  min-height: 10px;
  min-width: 10px; }

.tas-pikto-title {
  display: inline-block;
  position: relative;
  background: no-repeat top left;
  background-size: cover;
  width: 20px;
  height: 20px;
  opacity: 0.6;
  margin-right: 3px; }

.tas-pikto-calendar {
  background-image: url("../images/tickets/piktos/calendar.svg"); }

.tas-pikto-seat {
  background-image: url("../images/tickets/piktos/chair.svg"); }

.tas-pikto-address {
  background-image: url("../images/tickets/piktos/mail.svg"); }

.tas-radio {
  display: inline-flex;
  align-items: center;
  justify-content: center; }

.tas-radio-outer {
  display: inline-flex;
  width: 20px;
  height: 20px;
  border-radius: 20px;
  border: solid 1px silver;
  padding: 15%; }

.tas-radio-inner {
  display: inline-flex;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  border: solid 1px transparent;
  margin: auto; }

.tas-radio-active .tas-radio-inner {
  border: solid 1px silver;
  background-color: #0bcb9a; }

.tas-radio:hover .tas-radio-inner {
  border: solid 1px silver; }
